.modal {
  background-color: #1C1C21;
  width: 720px;
  border-radius: 40px;
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  position: fixed;
}

.btn {
  position: absolute;
  top: 60px;
  right: 40px;
  background: none;
  border: none;
}